<style>
  
</style>

<template>
  <div>
    <!-- 搜索框 -->
    <div class="search-form-wraper">
      <el-form :model="searchContent" label-position="top">
        <el-form-item label="所属门店">
          <el-select class="search-input" v-model="searchContent.storeNo" filterable placeholder="请选择所属门店" clearable size="small" @change="doSearchHandel(true)">
            <el-option
              v-for="item in storeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="SKU编码/商品名称">
          <el-input class="search-input" v-model.trim="searchContent.goodsNameErpNo" placeholder="SKU编码/商品名称" size="small" @keyup.enter.native="doSearchHandel(true)" clearable></el-input>
        </el-form-item>
        <el-form-item label="问题类型">
          <el-select class="search-input" v-model="searchContent.errorCode" placeholder="请选择订单状态" clearable size="small" @change="doSearchHandel(true)">
            <el-option :label="item.label" :value="item.value" :key="item.value" v-for="item in errorCodeOptions"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doSearchHandel(true)" size="mini">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    
    <!-- 搜索结果 -->
    <div class="table-wraper">
      <div class="sp-handel-w">
        <el-button class="sup-mini" size="mini" icon="el-icon-delete" :disabled="checkedList.length === 0"  @click="batchDeleteHandel">删除</el-button>
        <el-button class="sup-mini" size="mini" icon="el-icon-upload2" :disabled="checkedList.length === 0"  @click="batchUpdateHandel">同步</el-button>
      </div>
      <el-table
        class="fixed-height"
        v-loading="loading"
        :data="tableData"
        @select="handleSelect"
        @select-all="handleSelectAll"
        empty-text="没有查询到您想要的数据"
        style="width:100%;"
        size="small">
        <el-table-column
          type="selection">
        </el-table-column>
        <el-table-column min-width="120px" label="门店">
          <template slot-scope="scope">
            <div>
              <div>{{scope.row.storeName || '--'}}</div>
              <div class="erp-p-no">ERP编码：{{scope.row.storeNo || '--'}}</div>
            </div>
          </template>     
        </el-table-column>
        <el-table-column min-width="160px" label="门店商品">
          <template slot-scope="scope">
            <div>
              <div>{{scope.row.skuName || '--'}}</div>
              <div class="erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
              <div class="erp-p-no" v-if="scope.row.outerProductId">三方商品ID：{{scope.row.outerProductId || '--'}}</div>
            </div>
          </template>     
        </el-table-column>
        <el-table-column min-width="120px" prop="errorDesc" label="问题类型"></el-table-column>
        <el-table-column min-width="120px" prop="syncStatusStr" label="同步状态"></el-table-column>
        <el-table-column min-width="120px" prop="syncStatusDesc" label="失败原因"></el-table-column>
        <el-table-column
          align="center"
          fixed="right"
          label="操作"
          width="80">
          <template slot-scope="scope" size="small">
            <!-- errorCode: '', // 错误类型(101:本地不存在,三方存在; 201:信息不一致; 301:本地存在,三方不存在) -->
            <div v-if="scope.row.errorCode === 301 || scope.row.errorCode === 201">  
              <el-button @click="updateItem(scope.row)" type="text" size="mini" icon="el-icon-upload2">同步</el-button>
            </div>
            <div v-if="scope.row.errorCode === 301 || scope.row.errorCode === 101">
              <el-button @click="deleteItem(scope.row)" type="text" size="mini" icon="el-icon-delete">删除</el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页条 -->
      <el-pagination
        v-if="total>0"
        class="tc mt20"
        background
        @size-change="changeSize"
        @current-change="changeCurrent"
        :current-page.sync="currentPage"
        :page-sizes="[10, 50, 100, 150, 200]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  const requestUrl = {
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    // 查询列表
    searchList: '/plateform/a/store/goods/error/search',
    // 批量删除
    batchDel: '/plateform/a/store/goods/error/batchDel',
    // 批量同步
    batchSync: '/plateform/a/store/goods/error/batchSync'
  }
  export default {
    data () {
      return {
        loading: false,
        currentPage: 1,
        pageSize: 10,
        total: 0,
        searchContent: {
          platformId: 4, // 平台ID 2-京东到家，3-美团外卖，4-饿百
          errorCode: '', // 错误类型(101:本地不存在,三方存在; 201:信息不一致; 301:本地存在,三方不存在)
          goodsNameErpNo: '', // 商品名称/erp商品编码
          storeNo: '' // 门店编码
        },
        errorCodeOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '中台有三方无',
            value: 301
          },
          {
            label: '三方有中台无',
            value: 101
          },
          {
            label: '商品信息不一致',
            value: 201
          }
        ],
        // 门店列表下拉框
        storeOptions: [],
        tableData: [],
        // 默认选中的
        checkedList: []

      }
    },
    mounted() {
      // 获取门店
      this.getStoreList()

      // 查询类表
      this.doSearchHandel(true)
    },
    methods: {
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(requestUrl.search).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  value: item.storeNo,
                  label: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }

              this.storeOptions = optionsArr
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [changeSize description]
       * @param    {[type]}                 value [description]
       * @return   {[type]}                       [description]
       */
      changeSize (value) {
        this.pageSize = value
        this.doSearchHandel()
      },
      /**
       * [changeCurrent 点击分页条]
       * @param    {[type]}                 value [当前页]
       * @return   {[type]}                       [description]
       */
      changeCurrent (value) {
        this.pageNum = value
        this.doSearchHandel()
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        let hash = {}
        let checkedListTemp = []

        if(utils.isKeyInArrayBykey(row.id, 'id', this.checkedList)) {
          this.checkedList = utils.removeItemByKeyVal('id', row.id, this.checkedList)
          return
        }else {
          checkedListTemp.push(row)
        }

        let checkedListTempArr = [...checkedListTemp, ...this.checkedList]
        this.checkedList = checkedListTempArr.reduceRight((item, next) => {
          hash[next.id] ? '' : hash[next.id] = true && item.push(next)
          return item
        }, [])
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        let hash = {}
        if(val.length > 0) {        
          // 全选
          for(let item of val) {
            this.checkedList.push(item)
          }
          // 去重
          this.checkedList = this.checkedList.reduceRight((item, next) => {
            hash[next.id] ? '' : hash[next.id] = true && item.push(next)
            return item
          }, [])
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let checkedListTemp = []
          for(let item of this.tableData) {
            checkedListTemp.push(item)
          }
          // 2.从要传递的参数中去掉这些重复的
          let checkedListArr = utils.getSubtract(this.checkedList, checkedListTemp, 'id')
          this.checkedList = checkedListArr && checkedListArr.length > 0 ? checkedListArr : [] 
        }
      },
      /**
       * [doSearchHandel 获取列表内容]
       * @param  {[type]} _param [description]
       * @return {[type]}        [description]
       */
      doSearchHandel(_param) {
        this.loading = true
        if(_param === true) {
          this.pageNum = 1
        }
        this.$http.post(requestUrl.searchList, { ...this.searchContent, pageNum: this.pageNum, pageSize: this.pageSize }).then(res => {
          this.loading = false
          if(res.code === 0) {
            // 获取表格数据
            if(res.data && res.data.result && res.data.result.length) {
              this.tableData = res.data.result            
            }else {
              this.tableData = []
            }

            this.total = res.data.total
            return 
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [deleteItem 删除单个]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(_row) {
        let deletItem = {
          cityId: Storage.get("cityId"),
          platformId: this.searchContent.platformId,
          id: _row.id,
          skuNo: _row.skuNo,
          storeNo: _row.storeNo,
          storePlatformGoodsId: _row.storePlatformGoodsId,
          errorCode: _row.errorCode
        }
        let _params = []
        _params.push(deletItem)
        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchDel, _params).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("删除成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [batchDeleteHandel 批量删除]
       * @return {[type]} [description]
       */
      batchDeleteHandel() {
        let _params = []
        let deletItem = {}
        for(let item of this.checkedList) {
          deletItem = {
            cityId: Storage.get("cityId"),
            platformId: this.searchContent.platformId,
            id: item.id,
            skuNo: item.skuNo,
            storeNo: item.storeNo,
            storePlatformGoodsId: item.storePlatformGoodsId,
            errorCode: item.errorCode
          }
          _params.push(deletItem)
        } 

        this.$confirm('您确定要删除所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchDel, _params).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("批量删除成功")
              this.checkedList = []
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [updateItem 单个同步]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      updateItem(_row) {
        let deletItem = {
          cityId: Storage.get("cityId"),
          platformId: this.searchContent.platformId,
          id: _row.id,
          skuNo: _row.skuNo,
          storeNo: _row.storeNo,
          storePlatformGoodsId: _row.storePlatformGoodsId,
          errorCode: _row.errorCode
        }
        let _params = []
        _params.push(deletItem)
        this.$confirm('您确定要同步所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchSync, _params).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("同步成功")
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      },
      /**
       * [batchUpdateHandel 批量更新同步]
       * @return {[type]} [description]
       */
      batchUpdateHandel() {
        let _params = []
        let deletItem = {}
        for(let item of this.checkedList) {
          deletItem = {
            cityId: Storage.get("cityId"),
            platformId: this.searchContent.platformId,
            id: item.id,
            skuNo: item.skuNo,
            storeNo: item.storeNo,
            storePlatformGoodsId: item.storePlatformGoodsId,
            errorCode: item.errorCode
          }
          _params.push(deletItem)
        } 

        this.$confirm('您确定要同步所选商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          utils.showLoading()
          this.$http.post(requestUrl.batchSync, _params).then(res => {
            utils.clearLoading()
            if(res.code === 0) {
              this.$message.success("批量同步成功")
              this.checkedList = []
              this.doSearchHandel()
              return 
            }
            throw new Error(res.msg)
          }).catch(e => {
            utils.clearLoading()
            this.$notify.error({
              title: '提示',
              message: e.message
            })
          })
        }).catch(() => {})
      }


    }
  }
</script>


